:root{
    --danger : rgb(216, 17, 17);
    --light-danger : rgba(190, 10, 10, 0.486);
    --primary : rgb(55, 55, 170);
    --light-primary : rgba(55, 55, 170, 0.4);
    --success : rgb(84, 172, 84);
    --info: rgb(179, 168, 24);
    --dark :rgba(27, 27, 27, 0.616);
    --white : white;
    --light-white : rgba(255, 255, 255, 0.226);
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    color: var(--white);
}

.AppContainer{
    background-image: url("../images/bg-noise-texture.png");
}
hr{
    margin: 25px auto;
    background-color: var(--white);
    border: 1px dashed var(--danger);
}
a{
    color: inherit;
    text-decoration: none;
}
a:hover{
    color: var(--light-white);
}
.Header h1{
    font-size: 4rem;
    color: var(--success);
}
.Header, 
.Landing,
.Footer{
    padding: 10px;
}

.Header{
    background-color: var(--light-white);
    text-align: center;
    height: 15vh;
}

.Landing,
.leftImg,
.rightImg,
.Sleft{
    background-repeat: no-repeat;
    background-position: center;
}

.Landing{
    background-color: var(--danger);
    background-image: url("../images/wolverine.png");
    min-height: 70vh;
    display: flex;
    justify-content: space-evenly;
}
.landingitem{
    width: 45vw;
    text-align: center;
    padding-top: 150px;
}
.leftImg{
    background-image: url("../images/wolverine.png"), url("../images/griffes-gauche.png");
}
.rightImg{
    background-image: url("../images/wolverine.png"), url("../images/griffes-droit.png");
}

.Footer{
    min-height: 15vh;
    background-color: var(--light-primary);
}

.btn{
    background-color: transparent;
    font-size: 1.5rem;
    padding: .25rem 1rem;
    color: var(--white);
    border: 1px solid var(--white) ;
}

.btn:hover{
    background-color: var(--white);
    color: var(--danger);
}
.btn:disabled{
    background-color: var(--light-white);
    color: var(--light-white);
    cursor: not-allowed;
}

.signupContainer,
.LoginContainer,
.ForgetPsContainer
{
    display: flex;
    justify-self: center;
    width: 80%;
    margin: 10px auto;
}
.SignupItem
{   
    width: 49%;
    height: 67vh;
    margin: 15px auto;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
}
.LoginItem,
.forgetItem{
    width: 49%;
    height: 60vh;
    margin: 10px auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
}

.LoginItem.Sleft{
    background-image: url(../images/spiderman.png);
}
.forgetItem.Sleft{
    background-image: url(../images/deadpool.png);
}

.Sleft{
    background-image: url("../images/ironman.png");
}

/* form signup*/
.formItem{
    margin-top: 25px;
    position: relative;
    width: 100%;
}

.formItem input{
    background-color: transparent;
    border: none;
    outline: none;
    color: var(--danger);
    border-bottom: 1px dashed var(--white);
    font-size: 1.3rem;
    width: 100%;
    margin-top: 15px;
}
.formItem label{
    position: absolute;
    color: var(--light-white);
    top: 0;
    left: 0;
}
.formItem input:focus + label{
    top: -15px;
}
.btnCenter{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}
.formContainer p {
    text-align: start;
}

.welcomeContainer{
    width: 100%;
    min-height: 70vh;
}
.logoutContainer{
    display: flex;
    justify-content: flex-end;
    padding: 25px;
}
.switch{
    position: relative;
    width: 70px;
    height: 40px;
}

.switch span{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: var(--success);
    border-radius: 10px;
}

.witch input{
    opacity: 0;
}
.switch input:checked + span{
    background-color: var(--danger);
}

.switch input:checked + span::before{
    transform: translateX(30px);
    transition: transform .8s ease-in-out;
}

.switch span::before{
    position: absolute;
    content: "";
    width: 30px;
    height: 30px;
    background-color: var(--white);
    border-radius: 5px;
    top: 5px;
    left: 4px;
    cursor: pointer;
}

.containerLevel,
.containerProgress,
.containerQuiz
{
    width: 90%;
    margin: 10px auto;
    padding: 10px;
    border-radius: 5px;
}

.containerLevel{
    position: relative;
}
.score{
    position: absolute;
    top: 0;
    right: 0;
    width: 70px;
    padding: 10px;
    background-color: var(--white);
    border-radius: 5px;
    color: var(--danger);
    text-align: center;
    font-size: 1.5rem;
}

.containerProgress{
    display: flex;
    flex-flow: column nowrap;
    width: 100%;
}
.containerProgress div{
    display: flex;
    justify-content: space-around;
}
.progItem{
    background-color: var(--light-danger);
    padding: 10px;
    border-radius: 5px;
    font-size: 1.2rem;
}
.OptionQuiz {
    margin: 5px auto;
    background-color: var(--light-white);
    padding: 5px;
    cursor: pointer;
    border-radius: 5px;
    color: var(--light-white);
}
.containerQuiz h5{
    padding: 10px;
    text-align: center;
}
.OptionQuiz:hover{
    background-color: var(--light-danger);
    color: var(--white);
}

.select{
    background-color: var(--light-danger) ;
    text-transform: uppercase;
    color: var(--white);
}
.pbarContainer{
    margin-top: 10px;
}
.progresbar{
    position: relative;
    height: 30px;
    width: 75%;
    border-radius: 10px;
    border: 1px solid var(--light-white);
    box-shadow: -1px 2px 8px 3px rgba(211, 209, 209, 0.3) inset;
}
.bar{
    position: absolute;
    left: 1px;
    top: 2px;
    bottom: 2px;
    background-color: var(--success);
    border-radius: 10px;
}
.gamesms{
    background-color: transparent;
    padding: 10px;
    border-radius: 5px;
}
.table{
    border: 1px solid var(--light-white);
    color: white;
}
th.thinfo{
    display: flex;
    justify-content: center;
    align-items: center;
}
.table th{
    background-color: var(--light-danger);
    text-align: center;
}
.table td{
    background-color: transparent;
}
.table td, th{
    border: 1px solid var(--white);
}

.loaderContainer{
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
}
.loader{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 15px auto;
    border: 10px solid black;
    border-top: 10px solid var(--white);
    animation: loader 1s linear infinite;
}

@keyframes loader{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.msgContente{
    border: 1px solid var(--danger);
    background-color: var(--danger);
    border-radius: 10px;
}

.msgContente p,
.successmsg p
{
    margin: 5px 10px;
    text-align: center;
    font-size: 1rem;
}
.msgContente p
{
    color: var(--white);
}
.successmsg{
    border: 2px solid var(--white);
    background-color: transparent;
    border-radius: 10px;
}
.successmsg p
{ 
    color: var(--success);
}
.gameoverContainer{
    position: relative;
}
.modalContainer{
    position: absolute;
    background-color: rgba(248, 246, 246, 0.719);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.modal{
    position: absolute;
    display: flex;
    left: 50%;
    top: 50%;
    justify-content: flex-start;
    flex-flow: column nowrap;
    transform: translate(-50% -50%);
    width: 70vw;
    height: 75vh;
    transform: translate(-50%, -50%);
    border-radius: 10px;
}

.modalHead{
    background-color: var(--danger);
    flex: 1;  
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalBody{
    flex: 4;
    background-color: var(--white);
    display: flex;
    flex-flow: row nowrap;
    overflow: hidden;
}
.modalBody .description{
    color: black;
    margin: 10px;
}
.modalFooter{
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: black;
}

.modalFooter div{
    margin-right: 20px;
}
.modalItem{
    width: 48%;
}

.modalItem.description a{
    background-color: var(--danger);
    color: var(--white);
    border-radius: 5px;
    margin-right: 10px;
    padding: 5px;
}

.modalItem.description a:hover{
    background-color: var(--light-danger);
}